import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { WardrobeComponent } from './wardrobe.component';

const routes: Routes = [
  {
    path: '',
    component: WardrobeComponent,
  },
  {
    path: 'add-article',
    loadChildren: () =>
      import('./add-article-wardrobe/add-article-wardrobe.module').then(
        m => m.AddArticleWardrobeModule
      ),
  },
  {
    path: 'modify-article/:article',
    loadChildren: () =>
      import('./modify-article-wardrobe/modify-article-wardrobe.module').then(
        m => m.ModifyArticleWardrobeModule
      ),
  },
  {
    path: 'article-details/:id/:categoryId',
    loadChildren: () =>
      import('./article-details/article-details.module').then(m => m.ArticleDetailsModule),
  },
  {
    path: 'add-style',
    loadChildren: () => import('./add-style/add-style.module').then(m => m.AddStyleModule),
  },
  {
    path: 'style-details/:style',
    loadChildren: () =>
      import('./style-details/style-details.module').then(m => m.StyleDetailsModule),
  },
  {
    path: 'modify-style/:style',
    loadChildren: () => import('./modify-style/modify-style.module').then(m => m.ModifyStyleModule),
  },
  {
    path: 'style-reco',
    loadChildren: () => import('./style-reco/style-reco.module').then(m => m.StyleRecoModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class WardrobeRoutingModule { }
