import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { IonicSelectableComponent } from 'ionic-selectable';
import { Observable } from 'rxjs';
import { delay } from 'rxjs/operators';
import { LocalStorageConstants } from 'src/app/app.constants';
import { StorageService } from 'src/shared/services/storage.service';

@Component({
  selector: 'app-wardrobe-filters',
  templateUrl: './wardrobe-filters.component.html',
  styleUrls: ['./wardrobe-filters.component.scss'],
})
export class WardrobeFiltersComponent implements OnInit {
  range = { lower: 0, upper: 2000 };
  brandsMostUsed: any;
  colors: any[];
  brands: any;
  formGroup: FormGroup;

  constructor(private readonly storageService: StorageService, private readonly fb: FormBuilder) {}

  ngOnInit() {
    this.colors = this.storageService.getJSONItemFromLocalStorage(
      LocalStorageConstants.COLORS_AS_MAP
    );
    this.brandsMostUsed = this.storageService.getJSONItemFromLocalStorage(
      LocalStorageConstants.MOST_USED_BRANDS
    );
    this.brands = this.storageService.getJSONItemFromLocalStorage(LocalStorageConstants.BRANDS);
    this.formGroup = this.fb.group({
      priceRange: [{ lower: 0, upper: 2000 }],
      color: [''],
      state: [''],
      brand: [''],
    });
  }
  rangeChanged(event) {
    this.range.lower = event.detail.value.lower;
    this.range.upper = event.detail.value.upper;
  }

  onLoadColors(colors: any) {
    setTimeout(function () {
      return new Promise((resolve, reject) => {
        const radios = Array.from(
          document.getElementsByClassName('alert-checkbox-label')
        ) as HTMLElement[];
        resolve(radios);
      }).then(function (radios: any[]) {
        return new Promise((resolve, reject) => {
          radios.forEach((radio, index) => {
            radio.style.display = 'inline-flex';
            radio.style.justifyContent = 'space-between';
            radio.style.alignItems = 'center';
            radio.innerHTML = radio.innerHTML.concat(
              '<div style="width: 30px;height:30px;background:' +
                colors[index].rgb +
                ';border-radius: 50%;"></div>'
            );
          });
        });
      });
    }, 150);
  }

  getBrandsAsync(timeout = 250): Observable<any[]> {
    return new Observable<any[]>(observer => {
      observer.next(this.brands);
      observer.complete();
    }).pipe(delay(100));
  }

  filterBrands(brands: any[], text: string) {
    return brands.filter(brand => brand.fr_display.toLowerCase().indexOf(text) !== -1);
  }

  searchBrands(event: { component: IonicSelectableComponent; text: string }) {
    const text = event.text.trim().toLowerCase();
    event.component.startSearch();
    if (!text) {
      event.component.items = this.brandsMostUsed;
      event.component.endSearch();
      return;
    }

    this.getBrandsAsync().subscribe(allBrands => {
      event.component.items = this.filterBrands(allBrands, text);
      event.component.endSearch();
    });
  }
  onClear(event: { component: IonicSelectableComponent; items: any[] }) {
    event.component.clear();
  }

  onResetForm() {
    this.formGroup.reset({ priceRange: { lower: 0, upper: 2000 } });
  }
}
